import React from "react";
import search from "../assets/svg/search.svg";
import shopping from "../assets/svg/shopping_bag.svg";

import leftArrow from "../assets/svg/left_arrow.svg";
import mainClose from "../assets/svg/main_close.svg";

const Header = ({
  isLoggedIn,
  onLoginClick,
  pageType = "home",
  selectedCake,
  onBack,
}) => {
  const renderHeaderContent = () => {
    switch (pageType) {
      case "home":
        return (
          <>
            <div className="header-search-bar">
              <input type="text" placeholder=" " />
              <button className="search-icon">
                <img src={search} alt="" />
              </button>
            </div>
            <div className="header-actions">
              {!isLoggedIn ? (
                <>
                  <button onClick={onLoginClick} className="header-btn">
                    
                  </button>
                  <button className="header-btn"></button>
                </>
              ) : (
                <img src={shopping} alt="" />
              )}
            </div>
          </>
        );

      case "event":
        return (
          <>
            <button onClick={onBack} className="back-btn">
              <img src={leftArrow} alt=""></img>
            </button>
            <div className="header-title">
              <h2></h2>
            </div>
            <button onClick={onBack}>
              <img src={mainClose} alt=""></img>
            </button>
          </>
        );

      case "alarm":
        return (
          <>
            <button onClick={onBack} className="back-btn">
              <img src={leftArrow} alt=""></img>
            </button>
            <div className="header-title">
              <h2></h2>
            </div>
            <button onClick={onBack}>
              <img src={mainClose} alt=""></img>
            </button>
          </>
        );

      case "signature":
        return (
          <>
            <button onClick={onBack} className="back-btn">
              <img src={leftArrow} alt=""></img>
            </button>
            <div className="header-title">
              <h2></h2>
            </div>
            <button onClick={onBack}>
              <img src={mainClose} alt=""></img>
            </button>
          </>
        );

      case "detail":
        return (
          <>
            <button onClick={onBack} className="back-btn">
              <img src={leftArrow} alt=""></img>
            </button>
            <div className="header-title">
              <h2>{selectedCake?.name || ""}</h2>
            </div>
            <div className="header-actions">
              {isLoggedIn && <img src={shopping} alt="" />}
            </div>
          </>
        );

      case "points":
        return (
          <>
            <button onClick={onBack} className="back-btn">
              <img src={leftArrow} alt=""></img>
            </button>
            <div className="header-title">
              <h2></h2>
            </div>
            <button onClick={onBack}>
              <img src={mainClose} alt=""></img>
            </button>
          </>
        );

      default:
        return null;
    }
  };

  return (
    <div className={`header header-${pageType}`}>{renderHeaderContent()}</div>
  );
};

export default Header;
