import React, { useState, useEffect } from "react";
import { signatureCakesData } from "../data/cakesData";
import StoreBottomSheet from "../components/StoreBottomSheet";

import leftArrow from "../assets/svg/left_arrow.svg";
import mainClose from "../assets/svg/main_close.svg";

const SignaturePage = ({ onCakeClick, onBack }) => {
  const [isStoreSheetOpen, setIsStoreSheetOpen] = useState(false);
  const [selectedStore, setSelectedStore] = useState(null);

  //    
  useEffect(() => {
    setIsStoreSheetOpen(true);
  }, []);

  //   
  const handleStoreSelect = (store) => {
    console.log(" :", store);
    setSelectedStore(store);
    setIsStoreSheetOpen(false); //  
  };

  //    DetailPage 
  const handleCakeClick = (cake) => {
    if (onCakeClick) {
      onCakeClick(cake);
    }
  };

  return (
    <div className="page-content">
      <div className="detail-header">
        {/*    */}
        {selectedStore && (
          <div
            style={{
              margin: "0 16px",
              padding: "12px 16px",
              marginBottom: "12px",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              border: "1px solid #ebebebff",
              borderRadius: "8px",
            }}
          >
            <div>
              <p style={{ fontSize: "16px", color: "#000000ff" }}> </p>
              <p
                style={{
                  fontSize: "18px",
                  fontWeight: "bold",
                  color: "#000000ff",
                }}
              >
                {" "}
                {selectedStore.name}
              </p>
            </div>
            <button
              onClick={() => setIsStoreSheetOpen(true)}
              style={{
                padding: "6px 12px",
                fontSize: "14px",
                borderRadius: "4px",
                cursor: "pointer",
              }}
            >
              
            </button>
          </div>
        )}

        <div className="menu-button-wrap">
          <button></button>
          <button>/</button>
          <button></button>
          <button></button>
        </div>

        <div className="tabs">
          <button className="tab-btn"></button>
          <button className="tab-btn active"></button>
          <button className="tab-btn"></button>
        </div>
      </div>

      <div
        style={{
          padding: "16px",
          display: "flex",
          flexDirection: "column",
          gap: "12px",
        }}
      >
        {signatureCakesData.map((cake) => (
          <div
            key={cake.id}
            onClick={() => handleCakeClick(cake)}
            className="cake-item"
            style={{ cursor: "pointer" }}
          >
            <div
              style={{
                width: "100px",
                height: "100px",
                backgroundColor: cake.color,
                marginRight: "16px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                fontSize: "40px",
                overflow: "hidden",
              }}
            >
              <img
                src={cake.image}
                alt={cake.name}
                style={{ width: "100%", height: "100%", objectFit: "cover" }}
              />
            </div>
            <div style={{ flex: 1 }}>
              <p
                style={{
                  fontSize: "14px",
                  fontWeight: "bold",
                  color: "#FF552E",
                  marginBottom: "4px",
                }}
              >
                {cake.name}
              </p>
              <p
                style={{
                  fontSize: "16px",
                  color: "#6b7280",
                  marginBottom: "8px",
                }}
              >
                {cake.subtitle}
              </p>
              <p style={{ fontSize: "20px", fontWeight: "bold" }}>
                {cake.price.toLocaleString()}
              </p>
            </div>
          </div>
        ))}
      </div>

      {/*    */}
      <StoreBottomSheet
        isOpen={isStoreSheetOpen}
        onClose={() => setIsStoreSheetOpen(false)}
        onStoreSelect={handleStoreSelect}
      />
    </div>
  );
};

export default SignaturePage;
